@import url('reset.css');
@import url('variables.css');

html {
  height: 100%;
}

.wrapper {
  min-height: 100%;
  margin: 0 auto;

  display: grid;
  grid-template-rows: 60px 1fr 40px;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
  width: 1000px;
  grid-template-areas:
    "header header"
    "nav content"
    ". footer";

  font-family: 'Lato', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.2rem;
}

.content__wrapper {
  grid-area: content;
  width: 10fr;
  padding: 0;
}

.content__img {
  width: 10fr;
  height: 200px;
}

.content__desc {
  margin-top: 10px;
  padding: 40px;
  background-color: var(--color-grey);
}

.content__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}